import { NgClass } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { CardComponent } from '../../../../components/card/card.component';
import { FormErrorComponent } from '../../../../../../components/form-error/form-error.component';
import { FormFieldComponent } from '../../../../../../components/form-field/form-field.component';
import { ButtonLoadingDirective } from '../../../../../../directives/button-loading/button-loading.directive';
import { ColsField3Component } from '../../../../../../components/cols-field-3/cols-field-3.component';
import { emptyFieldValidator } from '../../../../../../validators/emptyField.validator';
import { UserStoreService } from '../../../../../../stores+/user.store';
import { DashboardApiService } from '../../../../../../services/api/dashboard-api.service';
import { AlertService } from '../../../../../../components/alert/alert.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'ca-employment',
  standalone: true,
  imports: [
    ReactiveFormsModule,
    NgClass,
    ButtonLoadingDirective,
    CardComponent,
    ColsField3Component,
    FormErrorComponent,
    FormFieldComponent,
  ],
  templateUrl: './employment.component.html',
  styles: ``,
})
export class EmploymentComponent implements OnInit {
  isSubmitted = false;
  loading = false;

  form: FormGroup = new FormGroup({});

  constructor(
    private fb: FormBuilder,
    private userStore: UserStoreService,
    private api: DashboardApiService,
    private alert: AlertService
  ) {}

  ngOnInit() {
    this.createForm();
  }

  createForm() {
    const { occupation, employer, employer_address } = this.userStore.user!;

    this.form = this.fb.group(
      {
        occupation: [occupation, [Validators.required, emptyFieldValidator()]],
        employer: [employer, [Validators.required, emptyFieldValidator()]],
        employer_address: [
          employer_address,
          [Validators.required, emptyFieldValidator()],
        ],
      },
      { updateOn: 'submit' }
    );
  }

  get occupation() {
    return this.form.get('occupation');
  }
  get employer() {
    return this.form.get('employer');
  }
  get employerAddress() {
    return this.form.get('employer_address');
  }

  handleSubmit() {
    this.isSubmitted = true;

    if (this.form.invalid) return;

    this.loading = true;

    this.api.updatePersonalEmploymentInfo(this.form.value).subscribe({
      next: ({ message, status }) => {
        this.loading = false;
        this.alert.open('success', { details: message, summary: status });
      },
      error: (error: HttpErrorResponse) => {
        this.loading = false;
        this.alert.open('danger', {
          details: error.error.message,
          summary: error.error.status + ' ' + error.status,
        });
      },
    });
  }
}
